import React, { useState } from "react";

export default function Textform(props) {
  const handleUppercase = () => {
    let newText = text.toUpperCase();
    setText(newText);
  };
  const handleLowercase = () => {
    let newText = text.toLowerCase();
    setText(newText);
  };
  const handleClearExtraSpaces = () => {
    let te = text.replace(/\s+/g, " ").trim();
    let newText = te.toLowerCase();

    setText(newText);
  };
  const handleClearAllSpaces = () => {
    let te = text.replace(/\s/g, "");
    let newText = te.toLowerCase();

    setText(newText);
  };
  const handleClearText = () => {
    setText("");
  };
  const copyToClipboard = () => {
    var copyText = document.getElementById("myInput");
    /* Select the text field */
    copyText.select();
    copyText.setSelectionRange(0, 99999); /* For mobile devices */
    /* Copy the text inside the text field */
    navigator.clipboard.writeText(copyText.value);
    
    setText(text);
  };
  const handleUpChange = (event) => {
    setText(event.target.value);
  };
  const [text, setText] = useState("");
  return (
    <>
      <div className="container mt-8">
        <div class="mb-3 mt-5">
          <label className="form-label text-white">{props.heading}</label>
          <textarea
            type="text"
            className="form-control"
            id="myInput"
            aria-describedby="emailHelp"
            rows="8"
            value={text}
            onChange={handleUpChange}
          ></textarea>
        </div>

        <button className="btn btn-primary" onClick={handleUppercase}>
          CHange to upperCase
        </button>
        <button className="btn btn-primary m-5" onClick={handleLowercase}>
          CHange to LowerCase
        </button>
        <button className="btn btn-primary m-5" onClick={handleClearText}>
          Clear text
        </button>
        <button
          className="btn btn-primary m-5"
          onClick={handleClearExtraSpaces}
        >
          Clear Extra Spaces
        </button>
        <button className="btn btn-primary m-5" onClick={handleClearAllSpaces}>
          Clear ClearAllSpaces
        </button>
        <button className="btn btn-primary m-5" onClick={copyToClipboard}>
          Copy Text
        </button>
      </div>
      <div className="container">
        <h1>Your Text contains </h1>
        <p>
          {text.length} = Character <br />
          And word is = {text.split(" ").length}
        </p>
      </div>
    </>
  );
}
